/*HEADER LAYOUT */
.header {
    height: 95vh;
    background-image: linear-gradient(to right bottom, rgba($color-primary-light, 0.801), rgba($color-primary-dark, 0.801)),url(../img/mountain-lake.jpeg);
    background-size: cover;
    background-position: top;
    -webikit-clip-path: polygon(0% 0%, 235vw 0%, 50% 100vh, 0% 75vh);
    clip-path: polygon(0% 0%, 235vw 0%, 50% 101vh, 0% 75vh);
    //HEADER LOGO
    & .logo-box {
        position: absolute;
        top: 6rem;
        left: 6rem;
        @include tablet-port {
            top: 2rem;
            left: 2rem; } }
    & .logo {
        height: 12.5rem;
        animation: moveInRight 1s ease-out 1s backwards;
        //anim-func, anim-dur, time-func, anim-delay=args fill-mode
        @include tablet-port {
            height: 10rem; }
        @include phone {
            height: 8rem; } }

    //HEADER TEXT
    & .text-box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
 } }        //by declaring this here it is telling the button to position itself in the center of the text box area




