.bg-video {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -2;
    opacity: .20;
    overflow: hidden;

    &__content {
        height: 100%;
        width: 100%;
        object-fit: cover; } }//makes the element fill the whole parent elemnent w/o messing with aspect ratio
